.contactus-container {
  position: relative;
  /* padding: 32px 64px; */
  width: auto;
  margin: auto;
  background-color: #c0d2d2;
}

.contactus-container img {
  width: 100%;
  display: block;
}

.contactus-container .figure1 {
  position: absolute;
  width: 50%;
  z-index: 1;
  top: 0;
  left: 0;
}

.contactus-text {
  /*This helps in using text over the image, because z-index will not work without relative position*/
  position: relative;
  padding: 18px 5vw;
  z-index: 2;
}

.contactus-form-container {
  display: flex;
  justify-content: space-between;
}

.contactus-heading {
  font-size: 4em;
  font-weight: normal;
  font-family: var(--font1);
  color: black;
  text-shadow: 3px 2px 0px grey;
  margin-bottom: 16px;
}

.contactus-container p {
  color: var(--color2);
  font-size: 24px;
  font-weight: bold;
  margin-top: 0px;
  margin-bottom: 30px;
}

.contactus-left {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.contactus-left input {
  padding: 15px;
  width: 90%;
  margin: 10px;
  margin-left: 0px;
  font-size: 18px;
  border-radius: 40px;
  border: 3px solid var(--color4);
  background-color: white;
  font-family: var(--font1);
}

.contactus-left input:focus {
  outline: none;
  border: 3px solid var(--color9);
}

.contactus-left textarea {
  padding: 15px;
  width: 90%;
  margin: 10px;
  margin-left: 0px;
  font-size: 18px;
  border-radius: 18px;
  border: 3px solid var(--color4);
  background-color: white;
  height: 130px;
  font-family: var(--font1);
}

.contactus-left textarea:focus {
  outline: none;
  border: 3px solid var(--color9);
}

.contactus-left button {
  display: block;
  margin: auto;
  padding: 15px;
  width: 50%;
  font-size: 18px;
  border-radius: 40px;
  border: 2px solid var(--color2);
  background-color: var(--color2);
  color: white;
  cursor: pointer;
  font-family: var(--font1);
  transition-duration: 0.3s;
}

.contactus-left button:hover {
  background-color: white;
  color: var(--color2);
  border: 2px solid var(--color2);
}

.contactus-right {
  position: relative;
  width: 50%;
  padding-top: 2rem;
  padding-bottom: 0;
  margin-left: 2rem;
  /* background-image: url(../../assets/contactUs_PostIt.png); */
}

/* Defined height and width so it change according to div */

.contactus-right .figure2 {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
}

.contactus-right .figure3_lines {
  display: none;
  position: absolute;
  width: 20%;
  height: 20%;
  top: -5%;
  left: 40%;
  /* For rotating 180 degree the image */
  transform: rotateX(180deg);
}

.contactus-right .contactus-tape {
  position: absolute;
  width: 110%;
  z-index: 50;
  left: -5%;
  top: 0;
}

.contactus-right-text {
  background-color: var(--color8);
  position: relative;
  z-index: 10;
  padding: 4rem;
  padding-bottom: 2rem;
}

/* .contactus-right-text img {
  position: absolute;
  z-index: -1;
  top: 0%;
  left: 0%;
  
  width: 100%;
  height: 100%;
} */

.contactus-right-text h2 {
  margin-top: 0px;
  text-align: center;
  text-decoration: underline;
  font-size: 30px;
  font-weight: 500;
}

.contactus-right-text ul {
  padding-left: 0px;
}

.contactus-right-text li {
  list-style: none;
  line-height: 2.2;
  font-size: 18px;
  font-weight: bold;
}

.contactus-right-text li:nth-of-type(3) {
  font-weight: normal;
}

.contactus-right-text li a {
  text-decoration: none;
  cursor: unset;
  font-size: 1rem;
  font-weight: normal;
  color: var(--color4);
}

@media only screen and (max-width: 900px) {
  .contactus-heading {
    font-size: 3em;
    text-shadow: 2px 1px 0px grey;
  }
  .contactus-container p {
    font-size: 20px;
  }
  .contactus-form-container {
    flex-direction: column;
  }
  .contactus-left {
    width: 100%;
    margin-bottom: 50px;
  }
  .contactus-left input {
    padding: 12px;
    margin: 8px;
    font-size: 14px;
  }
  .contactus-left textarea {
    padding: 12px;
    margin: 8px;
    font-size: 14px;
    height: 100px;
  }
  .contactus-left button {
    padding: 12px;
    font-size: 14px;
  }
  .contactus-right {
    width: 100%;
    margin-left: 0;
  }
}
