@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);
:root {
  --font1: "Neucha", cursive;
  --font2: "Cabin Sketch", cursive;
  --color1: #f9bb75;
  --color2: #e96671;
  --color3: #f79a90;
  --color4: #61878c;
  --color5: #413243;
  --color6: #f78a3f;
  --color7: #fffaf6;
  --color8: #fddec7;
  --color9: #56a0a2;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: "Neucha", cursive, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue";
  font-family: var(--font1), -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background-color: #413243;
  background-color: var(--color5);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #e96671;
  background-color: var(--color2);
}

/* For styling the scrollbar in mozilla firefox */
html {
  scrollbar-color: #e96671 #413243;
  scrollbar-color: var(--color2) var(--color5); /*thumb and track color*/
  scrollbar-width: 12px;
}

@media only screen and (max-width: 48em) {
  ::-webkit-scrollbar {
    display: none;
  }

  html {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}

.App-content {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  background-color: #b0bfc2;
}

/* Styling for loading */
.loading {
  font-size: 2em;
  font-family: var(--font1);
  color: #fae3d1;
  text-shadow: 3px 3px 0px #61878c;
}

/* Styling for text to HTML */
.textToHtml-container {
  /* So text lines cover whole div horizontally */
  text-align: justify;
  font-size: 1.5em;
}

.textToHtml-container h1 {
  font-size: 1em;
  font-weight: normal;
}

/* .article-content p {
  font-size: 1.5em;
} */

[id^="blockquote"] {
  padding: 0.5em 0em 0.5em 1em;
  border-left: 10px solid var(--color5);
  text-align: justify;
}

[id^="quote"] {
  margin-left: 1em;
  color: whitesmoke;
  quotes: "“" "„";

  /* It is used so there is no effect of margin-left on quotes */
  text-indent: -1em;
}

[id^="quote"]::before {
  content: open-quote;
  font-size: 3em;
  display: block;
}

[id^="quote"]::after {
  content: close-quote;
  font-size: 3em;
  display: block;
}

.article-container {
